&::-webkit-scrollbar {
	width: 8px;
}
&::-webkit-scrollbar-track {
	-webkit-border-radius: 10px;
	border-radius: 10px;
}
&::-webkit-scrollbar-thumb {
	opacity: 0.1;
	-webkit-border-radius: 10px;
	border-radius: 10px;
	background: rgba(0, 0, 0, 0.5);
	-webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.5);
}